<template>
  <transition name="move">
    <div v-show="showFlag" class="food" ref="food">
      <div class="food-content">
      <div class="image-header">
        <img :src="food.image">
        <div class="back" @click="hide">
          <span class="icon-arrow_lift"></span>
        </div>
      </div>
       <!-- 图片部分结束，详情内容开始 -->
      <div class="content">
        <h1 class="title">{{food.name}}</h1>
        <div class="detail">
          <span class="sell-count">月售{{food.sellCount}}份</span>
          <span class="rating">好评率{{food.rating}}%</span>
        </div>
        <div class="price">
          <span class="now">￥{{food.price}}</span>
          <span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
        </div>
        <div class="cartset-wrapper">
          <cartset :food="food"></cartset>
        </div>
        <transition name="fade">
        <div class="buy" @click="addShow" v-show="!food.count || food.count===0" ref="buy">加入购物车</div>
        </transition>
      </div>
      <!-- 购买选项结束，评价内容开始 -->
      <split v-show="food.info"></split>
      <div class="info" v-show="food.info">
        <h1 class="title">商品信息</h1>
        <p class="text">{{food.info}}</p>
      </div>
      <split></split>
      <!-- 评价部分开始 -->
      <div class="rating">
        <h1 class="title">商品评价</h1>
        <!-- 自定义属性selectType 、 onlyContent 、 des 、 ratings 传递给ratingselect组件-->
        <ratingselect :selectType="selectType" :onlyContent="onlyContent" :des="des" :ratings="food.ratings" @selecttype="setType" @content="setOnlycontent"></ratingselect>
        <div class="rating-wrapper">
          <ul v-show="food.ratings && food.ratings.length">
            <li v-show="needShow(rating.rateType, rating.text)" v-for="rating in food.ratings" class="rating-item border-1px">
              <div class="user">
                <span class="name">{{rating.username}}</span>
                <img :src="rating.avatar" class="avatar" width="12" height="12">
              </div>
              <div class="time">{{rating.rateTime | formatDate}}</div>
              <p class="text">
                <!-- 根据后台判断好评差评并显示拇指图标 -->
                <span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>
                {{rating.text}}
              </p>
            </li>
          </ul>
          <div class="no-rating" v-show="!food.ratings || !food.ratings.length">暂无评价</div>
        </div>
      </div>
    </div>
    </div>
  </transition>
</template>
<script type="text/ecmscript-6">
import Vue from 'vue'
import BScroll from 'better-scroll'
import cartset from '../cartset/cartset'
import split from '../split/split'
import ratingselect from '../ratingselect/ratingselect'
import { formatDate } from '../../common/js/Date'
const ALL = 2
export default {
  props: {
    food: {
      type: Object
    }
  },
  data () {
    return {
      showFlag: false,
      selectType: ALL,
      onlyContent: true,
      des: {
        all: '全部',
        positive: '推荐',
        negative: '吐槽'
      }
    }
  },
  methods: {
    setType (type) {
      this.selectType = type
      this.$nextTick(() => {
        this.scroll.refresh()
      })
    },
    setOnlycontent () {
      this.onlyContent = !this.onlyContent
      this.$nextTick(() => {
        this.scroll.refresh()
      })
    },
    show () {
      this.showFlag = true
      this.selectType = ALL
      this.onlyContent = true
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.food, {
            click: true
          })
        } else {
          this.scroll.refresh()
        }
      })
    },
    hide () {
      this.showFlag = false
    },
    // 更改this.food属性并自动刷新，显示cartset组件
    addShow () {
      if (!event._constructed) {
        return
      }
      Vue.set(this.food, 'count', 1)
    },
    // 判断 选项 ‘只看有内容评价’ 是否被点击 从而设置是否显示列表
    needShow (type, text) {
      if (this.onlyContent && !text) {
        return false
      }
      if (this.selectType === ALL) {
        return true
      } else {
        return type === this.selectType
      }
    }
  },
  filters: {
    formatDate (time) {
      let date = new Date(time)
      return formatDate(date, 'yyyy-MM-dd hh:mm')
    }
  },
  components: {
    cartset,
    split,
    ratingselect
  }
}
</script>
<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixin.styl"
.food
  position fixed
  left 0
  top 0
  bottom 48px
  z-index 30
  width 100%
  background #fff
  &.move-enter-active, &move-leave-active
    trasnsition all 0.5s linear
    opacity 1
  &.move-enter, &.move-leave-active
    transform translate3d(100%,0,0)
    opacity 0
  .image-header
    position relative
    width 100%
    height 0
    padding-top 100%
    img
      position absolute
      top 0
      left 0
      width 100%
      height 100%
    .back
      position absolute
      top 10px
      left 0
      .icon-arrow_lift
        display block
        padding 18px
        font-size 20px
        color #fff
  .content
    position relative
    padding 18px
    .title
      line-height 14px
      margin-bottom 8px
      font-size 14px
      font-weight bold
      color rgb(7,17,27)
    .detail
      margin-bottom 18px
      line-height 10px
      height 10px
      font-size 0
      .sell-count, .rating
        font-size 10px
        color rgb(147,153,159)
      .sell-count
        margin-right 12px
    .price
      font-weight bold
      line-height 24px
      .now
        margin-right 8px
        font-size 14px
        color rgb(240,20,20)
      .old
        text-decoration line-through
        font-size 10px
        color rgb(147,153,159)
  .cartset-wrapper
    position absolute
    right 12px
    bottom 12px
  .buy
    position absolute
    right 18px
    bottom 18px
    z-index 10
    height 24px
    line-height 24px
    padding 0 12px
    box-sizing border-box
    border-radius 12px
    font-size 10px
    color #fff
    background rgb(0,160,220)
    &.fade-enter-active,  &.fade-leave-active
      transition: all 0.2s
    &.fade-enter, &.fade-leave-active
      opacity: 0
  .info
    padding 18px
    .title
      line-height 14px
      margin-bottom 6px
      font-size 14px
      color rgb(7,17,27)
    .text
      line-height 24px
      padding 0 8px
      font-size 12px
      color rgb(77,85,93)
  .rating
    padding-top 18px
    .title
      line-height 14px
      margin-left 18px
      font-size 14px
      color rgb(7,17,27)
    .rating-wrapper
      padding: 0 18px
      height: auto
      .rating-item
        position: relative
        padding: 16px 0
        border-1px(rgba(7, 17, 27, 0.1))
        .user
          position: absolute
          right: 0
          top: 16px
          line-height: 12px
          font-size: 0
          .name
            display: inline-block
            margin-right: 6px
            vertical-align: top
            font-size: 10px
            color: rgb(147, 153, 159)
          .avatar
            border-radius: 50%
        .time
          margin-bottom: 6px
          line-height: 12px
          font-size: 10px
          color: rgb(147, 153, 159)
        .text
          line-height: 16px
          font-size: 12px
          color: rgb(7, 17, 27)
          .icon-thumb_up, .icon-thumb_down
            margin-right: 4px
            line-height: 16px
            font-size: 12px
          .icon-thumb_up
            color: rgb(0, 160, 220)
          .icon-thumb_down
            color: rgb(147, 153, 159)
      .no-rating
        padding: 16px 0
        font-size: 12px
        color: rgb(147, 153, 159)
</style>
